.TabPanel {
  display: flex;
  justify-content: center;
  height: 100%;
}

.TabPanel .ant-spin {
  display: flex;
  justify-content: center;
}

.tabs {
  width: 100%;
}

.ant-tabs-nav-wrap {
  display: flex;
  justify-content: center;
}
span.ant-input-search {
  margin-bottom: 20px;
}
.ant-input-affix-wrapper {
  width: 100%;
  margin: 0 auto;
}

@media screen and (max-width: 640px) {
  span.ant-input-search {
    margin-bottom: 10px;
  }
}
